.footer_icon {
  color: white;
  padding: 10px 10px;
  font-size: 24px;
}
.footer_icon:hover {
  color: #fdba66;
}

.footer_image {
  z-index: 2;
  box-shadow: 20px -15px #05595b;
}
.footer_content {
  max-width: 500px;
  padding-left: 100px;
}

.footer_content > p {
  font-size: 24px;
  font-weight: 400;
  padding-bottom: 15px;
}
.footer_content > h2 {
  font-size: 36px;
  font-weight: 500;
  padding-bottom: 31px;
}
.footer_content > h3 {
  font-size: 24px;
  font-weight: 500;
  padding-bottom: 35px;
}

.footer_right_h3 {
  min-width: 8rem;
  font-size: 20px;
  font-weight: 500;
  /* padding-bottom: 35px; */
}
.footer_right_text {
  font-size: 20px;
  font-weight: 400;
  /* padding-bottom: 15px; */
}

.footer_bottom_left > p {
  font-size: 16px;
  color: white;
  font-weight: 500;
  padding: 10px 0;
}
.footer_bottom_left > h3 {
  font-size: 24px;
  color: white;
  font-weight: 500;
}
.footer_bottom_right > p {
  font-size: 16px;
  color: white;
  font-weight: 500;
  padding: 10px 0;
}
.footer_bottom_right > h3 {
  font-size: 24px;
  color: white;
  font-weight: 500;
}
.button_text {
  padding: 10px;
  font-size: 16px;
}
