#info {
  background-color: #0d2427;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.info_content {
  position: relative;
  margin: 170px 0;
  display: flex;
  width: 787px;
}
.info_content_image_backgound {
  z-index: 5;
  border-radius: 39px;
  background: #05595b;
  /* padding: 20px; */
  width: 305px;
  height: 305px;
  box-shadow: 10px 10px rgba(253, 190, 106, 1);
}
.info_content_image_backgound > img {
  height: 305px;
}
.info_shadowBox {
  position: absolute;
  z-index: 2;
  top: 7.5px;
  left: 7.5px;
  border-radius: 39px;
  width: 305px;
  height: 305px;
  background-color: #fdbe6a;
}
.info_text {
  max-width: 500px;
  padding-bottom: 25px;
  font-weight: 400;
  font-size: 22px;
}
.info_content > span {
  padding-left: 2.5%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.info_readMore {
  font-weight: 400;
  font-size: 22px;
  color: #e2d784;
}
